.SideDrawer {
  position: fixed;
  width: 280px;
  max-width: 70%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 200;
  background-color: #ffffff;
  padding: 32px 0px;
  box-sizing: border-box;
  transition: all 0.5s ease-out;
}

@media (min-width: 500px) {
  .SideDrawer {
    display: none;
  }
}

.Open {
  transform: translateX(0);
}

.Close {
  transform: translateX(-100%);
}

.Logo {
  display: flex;
  justify-content: flex-start;
  margin: 0px 12px;
  height: 12%;
  margin-bottom: 16px;
}
